.header {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 1rem;
}

.header__todo-info {
  font-weight: 600;
  font-size: 1.2rem;
}

.header__title {
  margin: 0;
}

.count {
  color: #ccc;
}

.todo-info__count {
  display: inline-block;
}

.todo-info__count:last-of-type {
  margin-left: 10px;
}

.list-group-item {
  padding: 0.25rem 0.75rem;
}

.buttons-block {
  margin: 5px;
}

.buttons-block-btn {
  margin-right: 5px;
}

.done {
  text-decoration: line-through;
}

.important {
  color: steelblue;
  font-weight: bold;
}

.item-add-form {
  margin-top: 10px;
}

.root {
  max-width: 600px;
  width: 100%;
  margin: 100px auto;
}

